import {Component, Input, OnInit} from '@angular/core';
import {FileService, File} from "../../services/api/file.service";
import {ModalController} from "@ionic/angular";

@Component({
    selector: 'app-preview',
    templateUrl: './preview.component.html',
    styleUrls: ['./preview.component.scss'],
})
export class PreviewComponent implements OnInit {
    @Input() fileId: number;
    public file: File;
    public audioElement: HTMLAudioElement;


    constructor(private fileService: FileService, private modalController: ModalController) {
    }

    ngOnInit() {
        this.fileService.find(this.fileId).then(file => {
            this.file = file;
        });
    }

    async close() {
        this.modalController.dismiss();
    }

    loadAudioElement(element, autoPlay = true) {
        this.audioElement = element;
        console.log('load');
        this.audioElement.currentTime = 0;
        if (autoPlay) {
            this.audioElement.play();
        }
        element.addEventListener('timeupdate', this.listener.bind(this));
    }


    listener() {
        const element = this.audioElement;
        if (this.file) {
            if (element.currentTime >= element.duration - 0.416) {
                element.pause();
                console.log('/////');
                element.currentTime = 0;
            }
        }
    }

    toggle() {
        if (this.audioElement.paused) {
            this.audioElement.play();
        } else {
            this.audioElement.pause();
        }
    }


    getTimecode(str, type) {
        if (this.audioElement) {
            const time = type === 'current' ? this.convertTime(this.audioElement.currentTime) : this.convertTime(this.audioElement.duration);
            Object.keys(time).forEach(p => {
                str = str.replace('{' + p + '}', time[p]);
            });
            return str;
        }
        return "0:00";
    }

    convertTime(time) {
        const result = {h: null, m: null, s: null, hh: null, mm: null, ss: null, i: null, time: time};
        result.h = Math.floor(time / 60 / 60);
        result.m = Math.floor((time / 60) - (result.h * 60));
        result.s = Math.round(time - (result.m * 60) - (result.h * 60));

        result.hh = (result.h.toString().length < 2 ? '0' : null) + result.h;
        result.mm = (result.m.toString().length < 2 ? '0' : null) + result.m;
        result.ss = (result.s.toString().length < 2 ? '0' : null) + result.s;

        return result;
    }

}
